export class Place{ 

    brokerName  : string;
    brokerLogo  : string; 
    location    : string;
    interestRate: number;
    returnAmount: number;
    type        : string;
    createdData : string;

    constructor(
        brokerName = "", brokerLogo= "", location= "", interestRate= 0, returnAmount= 0, type= "", createdData= ""
    ){ 
        this.brokerName   = brokerName; 
        this.brokerLogo   = brokerLogo; 
        this.location     = location; 
        this.interestRate = interestRate; 
        this.returnAmount = returnAmount;
        this.type         = type; 
        this.createdData  = createdData;
    }


}


