import { Component, OnInit } from '@angular/core';
import { ComponentLinkService } from "../services/component-link.service";

import { Place } from "../data-model/place";

@Component({
  selector: 'app-display-table',
  templateUrl: './display-table.component.html',
  styleUrls: ['./display-table.component.scss']
})
export class DisplayTableComponent implements OnInit {

  tableData;

  constructor(
    private linkService : ComponentLinkService
  ) { 

    this.linkService.dataTable$.subscribe( data => { 
      this.tableData = data;
    } );

  }

  ngOnInit() {
  }

}


