import { Injectable } from '@angular/core';
import {Subject, BehaviorSubject} from "rxjs";
import { Place } from "../data-model/place";
import {Search} from "../data-model/search"; 

@Injectable()
export class ComponentLinkService {

  private dataTable = new BehaviorSubject<Place[]>([new Place()]);
  dataTable$ = this.dataTable.asObservable();
  updateDataTable( data : Place[] ){ 
    this.dataTable.next( data );
  }

  private searchTable = new Subject<Search>();
  searchTable$ = this.searchTable.asObservable();
  sendNewSearch( params : Search ){ 
    this.searchTable.next( params );
  }


}



