import { Component, HostListener } from '@angular/core';

import { ApiClientService } from "./services/api-client.service";
import {Place} from "./data-model/place";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';
  heading; 

  mobile = false;

  searchable     = [];
  viewable;
  seniorAccounts = [];
  taxFree        = []; 
  highInterest   = [];
  youthAccounts  = [];

  pageHeight = 0;


  constructor(
    private apiClient : ApiClientService
  ){ 

  
    this.pageHeight = window.screen.height;

    apiClient.getHighInterest().subscribe( data => { 
      
      data.map( place => {
        let currMap = new Place(
            place.brokerName, 
            place.brokerLogo, 
            place.location, 
            place.interestRate, 
            place.returnAmount,
            place.type, 
            place.createdData
          )

        this.highInterest.push( currMap );  
        this.searchable.push( currMap );
      });

    });

    apiClient.getSeniorAccounts().subscribe( data => { 
      
      data.map( place => { 
        let currMap = new Place(
            place.brokerName, 
            place.brokerLogo, 
            place.location, 
            place.interestRate, 
            place.returnAmount,
            place.type, 
            place.createdData
          )

        this.seniorAccounts.push( currMap );
        this.searchable.push( currMap );  
      });

    } );

    apiClient.getTaxFree().subscribe( data => { 
      data.map( place => { 
        let currMap = new Place(
            place.brokerName, 
            place.brokerLogo, 
            place.location, 
            place.interestRate, 
            place.returnAmount,
            place.type, 
            place.createdData
          )

        this.taxFree.push( currMap );
        this.searchable.push( currMap );  
      });
    } );

    apiClient.getYouthAccounts().subscribe( data => { 
      data.map( place => { 
        let currMap =new Place(
            place.brokerName, 
            place.brokerLogo, 
            place.location, 
            place.interestRate, 
            place.returnAmount,
            place.type, 
            place.createdData
          )

        this.youthAccounts.push( currMap );  
        this.searchable.push( currMap );  
      });
    } );

    this.viewable = this.seniorAccounts;
    this.heading = "Senior Accounts";
  }
  
  ngOnInit() {
    if (window.screen.width < 600) { // 768px portrai
      this.mobile = true;
    }
  }

  @HostListener('window:resize', ['$event'])
    onResize(event) {
      if( event.target.innerWidth < 600){ 
        this.pageHeight = window.screen.height;
        this.mobile = true;
      }
      else if( event.target.innerWidth > 600 ){ 
        this.pageHeight = window.screen.height;
        this.mobile = false;
      }
    }

  seniorClick(){ 
    this.viewable = this.seniorAccounts;
    this.heading = "Senior Accounts";
  };
  taxFreeClick(){ 
    this.viewable = this.taxFree;
    this.heading = "Tax Free Accounts";
  }
  highInterstClick(){
    this.viewable = this.highInterest; 
    this.heading = "High Interest Accounts";
  }
  youthClick(){ 
    this.viewable = this.youthAccounts;
    this.heading = "Youth Accounts";
  }

}



